$primary-color: #47bae0;
$complimentary-color: #e06d47;
$secondary-color: #47e0ba;
$font-light: #F0F0F0;
$font-dark: #2e2e2e;
$border: 1px solid #e06d47;

.login-form {
    background: $primary-color;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 18px;
    color: $font-dark;
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 350px;
    height: 100%;
    border: 0;
    border-radius: 70px;
    padding: 20px;
}

label{
    width:80%;
    display: block;
    margin: 0 auto;
    text-align: center;
    padding: 0;
}

.user-in, .password-in { 
    padding: 5px;
    margin: 5px 0;
    width: 100%;
    border: 0px;
    border-radius: 10px;
}

.login-button {
    background: $secondary-color;
    color: $font-dark;
    display: block;
    margin: 20px auto 0;
    border: $border;
    border-radius: 10px;
    width: 40%;
    padding: 5px 0;
    
}
